import swiper from '@/components/colorui/components/swiper.vue';
import uParse from '@/components/gaoyia-parse/parse.vue';
export default {
	data() {
		return {
			productId: 0,
			customerShow: false,
			swiperList: {},
			product: {},
			title: '',
			TabCur: 0,
			scrollLeft: 0,
			nav_id: 0,
			navList: {"0" : "赛事介绍", "1" : "赛事组别", "package" : "参赛套餐",  "goods" : "可选服务",},
			packageId: null,
			modalName: ''
		}
	},
	components: {
		swiper,
		uParse,
	},
	onShareAppMessage(res) {
		return {
			title: this.product.name,
			path: '/pages/product/index?id=' + this.product.id
		}
	},
	methods: {
		showTip(type){
			
			switch(type){
				case 0:
				    uni.showToast({
				    	title:'活动未开始报名！',
						icon: 'none'
				    })
				break;
				case 1:
				uni.showToast({
					title:'活动报名已结束！',
					icon: 'none'
				})
				break;
			}
		},
		navTo(url) {
			uni.navigateTo({
				url:url
			})
		},
		hideCustomer(e) {
			this.customerShow = false
			console.log(this.packageId)
		},
		customerOpen() {
			this.customerShow = true
		},
		tip() {
			this.$api.msg('暂未开放');
		},
		goTop() {
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		},
		showModal(key) {
			console.log(key)
			this.packageId = key
		},
		hideModal(e) {
			this.packageId = null
			console.log(this.packageId)
		},
		preview(src, e) {
			// do something
		},
		navigate(href, e) {
			// do something
		},
		tabSelect(e) {
			this.nav_id = this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60

		},
		commit() {
			let product_id = this.product.id;
			uni.navigateTo({
				url: '/pages/commit/index?product_id=' + product_id,
			});
		},
		getProduct(id) {
			var _this = this;
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			this.$http.post('/api/activity/detail', {
				"id": id
			}).then(res => {
				var data = res.data.data
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					console.log(res)
					_this.swiperList = data.image;
					_this.product = data;
					if (data.package.length == 0) {
						// _this.navList.splice(2, 1);
						delete _this.navList.package
						console.log(_this.navList)
					}
					if (_this.product.isCate) {
						// （准备资料）（签证名额）（签证类别）
						_this.navList = ['准备资料', '签证名额', '签证类别'];
					}
					 uni.hideLoading();
					// console.log(data.package.length)
				}
			}).catch(err => {
				console.log(err);
				uni.showToast({
					title: '网络错误！',
					icon: 'none'
				});
				uni.hideLoading();
			})
			// uni.hideLoading();
			// uni.request({
			// 	url: _this.httpUrl + '/api/activity/detail',
			// 	header: {
			// 		'content-type': 'application/x-www-form-urlencoded',
			// 	},
			// 	data: {
			// 		"id": id
			// 	},
			// 	method: 'POST',
			// 	dataType: 'json',
			// 	success: (res) => {
			// 		var data = res.data.data
			// 		if (res.data.code != 1) {
			// 			uni.showToast({
			// 				title: res.data.info,
			// 				icon: 'none'
			// 			});
			// 		} else {
			// 			console.log(res)
			// 			_this.swiperList = data.image;
			// 			_this.product = data;
			// 			if (data.package.length == 0) {
			// 				_this.navList.splice(2, 1)
			// 			}
			// 			console.log(data.package.length)
			// 		}
			// 	}
			// });
		},
		//获取页面数据
		getPageData: function(id) {
			var _this = this;
			_this.getProduct(id);
		},
	},
	onLoad: function(option) {
		// option.id = 8;
		this.getPageData(option.id);
	},
}
